<template>
    <div class="shop">
        <router-link to="/shop/shopa">Â∑¶ËæπÈ°µÈù¢</router-link>| |
        <router-link to="/shop/shopb">Âè≥ËæπÈ°µÈù¢</router-link>
        <table>
            <thead>
                <tr>
                    <th>ÂÖ®ÈÄâ<input type="checkbox" :checked='checkchange' @click="btnmax"></th>
                </tr>
            </thead>
            <tbody v-for="(i,v) in list" :key='v'>
                <tr>
                    <td><input type="checkbox" :value="i.name" v-model="i.checked"></td>
                    <td>ÂïÜÂìÅÂêçÔºö{{i.name}}</td>
                    <td>Âçï‰ª∑Ôºö{{i.mon}}Ôø•</td>
                    <td>
                        <button @click="add(v,-1)">-1</button>
                        <span>{{i.num}}</span>
                        <button @click="add(v,+1)">+1</button>
                    </td>
                    <td>ÊÄªËÆ°Ôºö{{i.mon*i.num}}Ôø•</td>
                    <td>
                        <button type="button" @click="del(v)">Âà†Èô§</button>
                    </td>
                </tr>
            </tbody>
            <span>ÊÄª‰ª∑:{{max}}Ôø•</span>
        </table>
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                list:[
                    {name:'ËãπÊûú',mon:5,num:1,checked:false},
                    {name:'È¶ôËïâ',mon:89,num:1,checked:false},
                    {name:'üå∞',mon:5,num:1,checked:false},
                    {name:'ËéΩÂì•',mon:13,num:1,checked:false},
                ]
            }
        },
        computed:{
            max(){
                let total=0;
                this.list.forEach((p)=>{
                    if(p.checked){
                        total+= p.mon*p.num;
                    }
                })
                return total;
            },
            checkchange(){//ÂÖ®ÈÄâ
                return this.list.every(i=>i.checked)
            }

        },
        methods:{
           btnmax(){
             this.list.forEach((i)=>{
                    if(i.checked){
                        i.checked=!i.checked
                    }else{
                        i.checked=!i.checked
                    }
             })
           },

            del(v){
                 this.list.splice(v,1)
            },
            add(v,num){
                this.list[v].num+=num;
                if(this.list[v].num<0){
                     this.list[v].num=0;
                }
            },

        },
    }
</script>

